  height: auto;
  width: 100%;

  border-left: 8px solid #E97816;
  box-shadow: 0 0 3px 3px rgba(0, 0, 0, 0.1);

  .card-title {
    width: 100%;
    padding: 15px 20px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    background-color: #385590;
    color: white;
    font-size: 1.2rem;

    h4 { margin: 0; }
  }

  .card-subtitle {
    width: 100%;
    padding: 10px;

    display: grid;
    grid-gap: 10px 15px;
    grid-template-columns: auto 1fr;
    align-items: center;

    background-color: #DDE7FD;
    color: black;
    font-size: 1.1rem;

    h5 {
      margin: 0;
      text-align: right;
    }
  }

  .card-body {
    padding: 10px;
